import React, { useEffect, useState } from 'react';
import { View, StyleSheet } from 'react-native';
import WebView from 'react-native-webview';
import Container from './components/Container';
import SanarTelemedicine from './SanarTelemedicine';

interface ISanarChat {
    onEndFlow: () => void,
    enable: boolean,
    lang: string,
    notification: any
    appointmentId,
    empId
}

const SanarChat = (props: ISanarChat) => {
    const [baseUrl, setBaseUrl] = useState('');
    const { notification } = props;
    useEffect(() => {
        if (props.enable) {
            if (SanarTelemedicine.session) {
                const CHAT_URL = `${SanarTelemedicine.session.chatUrl}/${notification.roomName}/${notification.uid}?token=${SanarTelemedicine.session.token}&lang=${props.lang||'en'}`;
                setBaseUrl(CHAT_URL);
            }
        }
    })
    const onMessage = event => {
        console.log(baseUrl);
        if ((!event.canGoBack && event.data && event.data.includes('home')) || (event.canGoBack && event.data && event.data.includes('home'))) {
            setBaseUrl('');
            props.onEndFlow();
        }
    }
    
    if (!props.enable) {
        return (null);
    }
    return (
        <Container>
            <View style={styles.container}>
                <WebView
                    source={{
                        uri: baseUrl,
                    }}
                    style={styles.container}
                    // incognito
                    onMessage={(event) => onMessage(event.nativeEvent)}
                    injectedJavaScript={`
                        (function() {
                            function wrap(fn) {
                            return function wrapper() {
                                var res = fn.apply(this, arguments);
                                window.ReactNativeWebView.postMessage(window.location.href);
                                return res;
                            }
                            }
                            history.pushState = wrap(history.pushState);
                            history.replaceState = wrap(history.replaceState);
                            history.go = wrap(history.go);
                            window.addEventListener('popstate', function() {
                                window.ReactNativeWebView.postMessage('navigationStateChange');
                            });
                        })();
                        true;
                    `}
                />
            </View>
        </Container>
    );
}

export default SanarChat;

const styles = StyleSheet.create({
    container: {
        height: '100%',
        width: '100%',
        zIndex: 8
        // flex: 1
    }
});